@font-face {
  font-family: 'Inter';
  src: url("../fonts/inter/Inter-Bold.woff2") format("woff2"), url("../fonts/inter/Inter-Bold.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Public Sans';
  src: url("../fonts/publickSans/PublicSans-Light.woff2") format("woff2"), url("../fonts/publickSans/PublicSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Public Sans';
  src: url("../fonts/publickSans/PublicSans-Bold.woff2") format("woff2"), url("../fonts/publickSans/PublicSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Public Sans';
  src: url("../fonts/publickSans/PublicSans-Regular.woff2") format("woff2"), url("../fonts/publickSans/PublicSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/quicksand-v30-latin-regular.woff2") format("woff2"), url("../fonts/quicksand-v30-latin-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}

* {
  margin: 0;
  box-sizing: border-box;
}

::after,
::before {
  box-sizing: border-box;
}

.container {
  max-width: 1530px;
  min-width: 300px;
  margin: 0 auto;
  padding: 0 30px;
}

.body {
  font-family: 'Public Sans','Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.img-resonsive {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #0A1F32;
}

a:hover {
  color: #73CBE8;
}

.btn--color-orange {
  cursor: pointer;
  border-radius: 20px 6px;
  border: 2px solid #FF813A;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  background-color: #FF813A;
}

.btn--color-orange:hover {
  background-color: transparent;
  border: 2px solid #FF813A;
  border-radius: 20px 6px;
  color: #FF813A;
}

.btn--color-orange:active {
  color: #FFFFFF;
  background-color: #FF813A;
  border-radius: 20px 6px;
}

.header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
  position: relative;
}

.header__logo::before {
  content: '';
  position: absolute;
  height: 69px;
  width: 180px;
  left: -30px;
  top: -20px;
  background-color: #F9F9FB;
  border-radius: 0 0 30px;
}

.header__logo-img {
  display: flex;
}

.header__logo-svg {
  z-index: 10;
}

.header__menu {
  position: absolute;
  top: 49px;
  right: 0;
  transform: translateX(-100%);
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease;
}

.header__menu-body {
  background-color: #E2E2E2;
  padding: 40px 30px;
  width: 330px;
}

.header__menu-links {
  display: flex;
  flex-direction: column;
  padding-bottom: 18px;
  border-bottom: 2px solid rgba(0, 52, 88, 0.04);
}

.header__menu-link {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 29px;
  color: #0A1F32;
  margin-bottom: 20px;
}

.header__menu-link:hover path {
  stroke: #73CBE8;
}

.header__menu-svg {
  margin-left: 8px;
}

.header__menu.show {
  visibility: visible;
  transform: translateX(0%);
  opacity: 1;
}

.header__acount {
  display: flex;
  align-items: center;
}

.header__login {
  position: absolute;
  right: 0;
  top: 354px;
  transform: translateX(-100%);
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease;
}

.header__login-body {
  display: flex;
  flex-direction: column;
  background-color: #E2E2E2;
  padding: 40px 30px;
  width: 330px;
}

.header__login-link {
  display: flex;
  align-items: center;
  align-self: center;
  font-size: 18px;
  line-height: 29px;
  color: #0A1F32;
  margin-bottom: 20px;
}

.header__login-svg {
  margin-right: 10px;
}

.header__login.show {
  visibility: visible;
  transform: translateX(0%);
  opacity: 1;
}

.header__register-btn {
  font-family: 'Public Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  padding: 10px 35px;
  background-color: #FF813A;
}

.header__lang-link {
  display: flex;
  align-items: center;
  align-self: center;
  font-size: 20px;
  line-height: 29px;
  color: #0A1F32;
}

.header__lang-link:hover path {
  stroke: #73CBE8;
}

.header__lang-svg {
  margin-left: 8px;
}

.header__burger {
  position: relative;
  cursor: pointer;
  width: 41px;
  height: 20px;
  margin-left: 21px;
}

.header__burger-span {
  width: 41px;
  height: 3px;
  background-color: #003458;
  position: absolute;
}

.header__burger-span:hover path {
  stroke: #73CBE8;
}

.header__burger-span:nth-child(1) {
  top: 0;
}

.header__burger-span:nth-child(2) {
  top: 50%;
  right: 0;
  width: 33px;
  transform: translatey(-50%);
}

.header__burger-span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 22px;
}

@media screen and (min-width: 800px) {
  .header__body {
    margin: 25px 0;
  }
  .header__logo-svg {
    width: 124px;
    height: 20px;
  }
  .header__logo::before {
    height: 79px;
    top: -25px;
    width: 255px;
  }
}

@media screen and (min-width: 1200px) {
  .header__body {
    margin: 18px 0;
  }
  .header__logo-svg {
    width: 104px;
    height: 17px;
  }
  .header__logo::before {
    left: -79px;
  }
  .header__menu {
    position: static;
    visibility: visible;
    opacity: 1;
    transform: translateX(0%);
  }
  .header__menu-body {
    background-color: transparent;
    padding: 0;
    width: 100%;
  }
  .header__menu-links {
    flex-direction: row;
    border: none;
    padding: 0;
  }
  .header__menu-link {
    white-space: nowrap;
    margin-bottom: 0;
    margin: 0 12px;
    font-size: 16px;
    line-height: 24px;
  }
  .header__menu-link:hover path {
    stroke: #73CBE8;
  }
  .header__lang-link {
    font-size: 16px;
  }
  .header__burger {
    display: none;
  }
  .header__login {
    position: static;
    transform: translateX(0%);
    visibility: visible;
    opacity: 1;
  }
  .header__login-body {
    flex-direction: row;
    background-color: transparent;
    padding: 0;
    width: 100%;
  }
  .header__login-link {
    display: flex;
    align-items: center;
    align-self: center;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 0;
  }
  .header__register-btn {
    font-size: 16px;
    line-height: 24px;
    margin: 0 15px;
  }
}

@media screen and (min-width: 1530px) {
  .header__logo::before {
    width: 470px;
    left: -294px;
  }
}

@media screen and (min-width: 1920px) {
  .header__menu-link {
    font-size: 18px;
    line-height: 29px;
  }
  .header__logo::before {
    width: 870px;
    left: -591px;
  }
  .header__logo-svg {
    width: 119px;
    height: 20px;
  }
  .header__login-link {
    font-size: 18px;
    line-height: 29px;
  }
  .header__register-btn {
    padding: 10px 35px;
    font-size: 18px;
    line-height: 29px;
  }
  .header__lang-link {
    font-size: 18px;
    line-height: 29px;
  }
}
